.main {
  display: grid;
  grid-template-columns: 1fr 2fr;
}

.imghere {
  position: fixed;
  left: 0px;
  top: 0px;
  width: 40%;
  height: 100%;
}
.mainlog {
  display: flex;
  justify-content: normal;
  flex-direction: column;
  align-items: center;
}
.Login{
