.forget {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  letter-spacing: 0.07rem;
  padding-left: 25rem;
  font-family: "Montserrat", sans-serif;
}
.mainbtn {
  display: flex;
  margin-top : 3%;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  width: 30%;
  height: 250px;
}
.mainbtn >  button {
  height: 2.8rem;
  width: 100%;
}
.mainbtn button:first-child {
  color: white;
  font-size: 1rem;
  height : 20px;
  width : 100%;
  display: flex;
  align-items: center;
  border-style: none;
}

.mainbtn button:nth-child(1) {
  background-color: #4267b2;
  border-style: none;
  width: 100%;
  font-size: 1.5rem;
  color: white;
  border-radius: 4px;
}
@media (max-width: 1070px) {
  .imghere {
    display: none;
  }

  .main {
    grid-template-columns: 0fr 2fr;
  }
}


@media (max-width: 520px) {
  .imghere {
    display: none;
  }

  .main {
    grid-template-columns: 0fr 2fr;
  }

  .btnup{
    width: 60%;
  }
}

@media (max-width: 400px) {
  .imghere {
    display: none;
  }

  .main {
    grid-template-columns: 0fr 2fr;
  }

  .btnup{
    width: 60%;
  }

  .mainbtn{
    padding-left: 15vw;
  }
  .mainbtn button:last-child {
    width: 75vw;
  }
}

